:root {
    --green: #6db46d;
}
* {
    box-sizing: border-box;
}

body {
    margin: 32px;
    background: #23252C;
    font-family: 'Inter', sans-serif;
    color: #F5F5F5;
}

#root {
    display: flex;
    align-items: center;
    justify-content: center;
}

.card {
    background: #1A1B21;
    width: 320px;
    border-radius: 16px;
    overflow: hidden;
    box-shadow: black 0 0 64px;
    border: 1px solid #23252C;
}

.photo {
    width: 100%;
}

.main-content {
    padding: 20px 35px 35px 35px;
}

.name-tag {
    text-align: center;
}

.name-tag-name {
    margin: 0;
}

.name-tag-business-title {
    margin: 4px 0 8px 0;
    font-size: 12px;
    color: var(--green);
}

.name-tag-webpage {
    display: block;
}

a {
    font-size: 10px;
    font-style: normal;
    color: #F5F5F5;
    text-decoration: none;
    transition: 0.25s;
}


a:link {
    text-decoration: none;
}

a:visited {
    text-decoration: none;
}

a:hover {
    color: var(--green);
    cursor: pointer;
    text-decoration: none;
}

a:active {
    text-decoration: none;
}

.email-button {
    display: flex;
    margin-top: 16px;
    padding: 8px;
    width: 100%;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    color: #374151;
}

.email-button-label {
    margin: 0 0 0 8px;
}

.blurb {
    margin: 24px 0 0 0;
}

.blurb-header {
    margin: 0;
}

.blurb-body {
    margin: 4px 0;
    font-size: 10px;
    color: #DCDCDC;
}

.footer {
    display: flex;
    padding: 24px;
    align-items: center;
    justify-content: space-evenly;
    background: #161619;
}

.footer-link:hover {
    cursor: pointer;
}